import React from "react";

const Navbar = props => (
  <nav>
    <div className="nav-wrapper purple lighten-2">
      <a className="brand-logo left hide-on-small-and-down">Calming Clicks</a>
      <ul id="nav-mobile" className="right">
        <li>
          <div className="brand-logo flow-text center">
            Current Score: {props.currentScore}
          </div>
        </li>
        <li>
          <div className="brand-logo right hide-on-small-and-down">
            Top Score: {props.topScore}
          </div>
        </li>
      </ul>
    </div>
    <div className="nav-wrapper purple lighten-3">
      <div
        style={{ color: props.getGameStateStyle }}
        className="brand-logo center hide-on-med-and-down"
      >
        {props.getGameState}
      </div>
    </div>
  </nav>
);

export default Navbar;



